import React from 'react';
import { BiSolidUpArrow } from 'react-icons/bi';
import logo from '../../assets/images/AI.jpeg';
import { useContext, useState } from 'react';
import { ProjectContext } from '../../context/ProjectContext';

const SearchCard = ({ project, className }) => {
  const { projectName, description, address, tags, votes, _id } = project || { projectName: '', description: '', address: '', tags: [], votes: 0, _id: ''};
  const truncatedAddress = address.substring(0, 6) + '.....' + address.substring(address.length - 6);

  const { handleUpvoteProject } = useContext(ProjectContext)
  const [upvotes, setUpvotes] = useState(votes);
  const handleSetUpvotes = (project_id) => {
    handleUpvoteProject(project_id);
    setUpvotes(upvotes+1);
  }

  return (
    <div className={`flex md:flex-row relative py-2 md:py-5 bg-slate-50 shadow-sm border-[1px] rounded-md ${className}`}>
      <img src={logo} className='md:h-28 h-20 w-20 md:w-36 rounded-md border-[1px] p-2 md:ml-4 md:mr-6 self-center' />
      <div className='md:flex-grow ml-2 md:-ml-4 max-w-52 md:max-w-96'>
        <div className='flex md:flex-row space-x-2 md:items-center md:mb-2'>
          <h3 className='md:text-2xl font-semibold'>{projectName}</h3>
          <p className='md:text-2xl text-gray-400 font-bold'>#</p>
        </div>
        <p className='truncate w-1/6 text-xs md:text-sm'>{description}</p>
        <p className='text-xs md:text-sm'>{truncatedAddress}</p>
        <div className='flex flex-wrap space-x-2 pt-4'>
          {tags?.map((tag, index) => (
            <p key={index} className='px-2 md:font-semibold rounded-sm bg-secondarylight text-xs md:text-sm'>{tag}</p>
          ))}
        </div>
      </div>
      <div onClick={() => handleSetUpvotes(_id)} className='absolute md:w-24 md:mt-10 my-auto right-6 md:right-12 bottom-6 md:bottom-auto border-[1px] flex md:px-6 px-2 md:py-2 py-1 rounded-sm md:rounded-md text-gray-500 space-x-2'>
        <BiSolidUpArrow className='md:h-4 h-2 md:w-4 w-2 my-auto' />
        <p className='text-xs md:text-sm'>{upvotes}</p>
      </div>
    </div>
  );
};

export default SearchCard;
